import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './MainPage.css';
import axios from 'axios';

export const MainPage = () => {
    const [threads, setThreads] = useState([]);

    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/category/"
        }).then((res) => {
            setThreads(res.data);
        })
    }, [])

    return (
        <>
           <h1>Main Page</h1>
            {threads ? threads.map((elem) =>
                <Link to={{ pathname: `/category/${elem.id}`, fromDashboard: false }}>{elem.name}</Link>
            ) : <h3>Loading...</h3>}
        </>
    )
}